import moment from 'moment';
import React, { useState, useEffect } from 'react';
import CountdownCard from './card/CountdownCard';
import _ from 'lodash';
import { decompress } from 'compress-json';

import './App.css';

function App() {
  const displayFormat = 'YYYY MMM DD hh:mm:ss a';

  const dataFormat = 'YYYY/MM/DD hh:mm:ss';

  const [eventData, setEventData] = useState([]);
  const [timer, setTimer] = useState(0);
  const [lastUpdated, setLastUpdated] = useState();
  const [version, setVersion] = useState();

  useEffect(() => {
    fetch('data/data.json')
      .then((r) => r.json())
      .then((json) => {
        const decompressed = decompress(json);
        const eventData = _.chain(decompressed.events)
          .map(event => {
            const startDate = moment.utc(event.start_time, dataFormat)
            const endDate = moment.utc(event.end_time, dataFormat)
            return { ...event, startDate, endDate }
          })
          .orderBy(event => new moment(event.startDate))
          .value();

        setLastUpdated(moment(Date.parse(decompressed.lastUpdated)))
        setVersion(decompressed.version)
        setEventData(eventData)
      });

    const timer = setInterval(() => {
      setTimer(1);
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    setTimer(0);
  }, [timer]);

  const content = (
    <div className='card-container'>
      {
        eventData.map((event, index) => {
          const endDate = moment.utc(event.end_time, dataFormat)
          return (endDate.diff(moment()) > 0) ? <CountdownCard key={`${event.event_name}-${index}`} event={event} /> : null;
        })
      }
    </div>
  )
  return (
    <div className="App">
      {content}
      {lastUpdated && (<div>Last updated: {lastUpdated.local().format(displayFormat)}</div>)}
      {version && (<div>Version: {version}</div>)}
    </div>
  );
}

export default App;
